
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default{
components: {
XHeader,Toast
},
data(){
return {
noData: false,
pageIndex: 1,
pageSize: 15,
list: [],
}
},
mounted() {
this.getPremaritalCheckList(true);
},
methods: {

getPremaritalCheckList(upOrDown) {
if (upOrDown) {
this.pageIndex = 1;
}
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "marriageDiagnosisService",
"MSH.2": "list",
"CHZBM": this.$root.loginInfo.CHZBM,
"pageIndex": this.pageIndex,
"pageSize": this.pageSize,
'GRDAID':this.$root.loginInfo.grdaid,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.pageNumber == 1) {
this.pageIndex = 1,
this.list = [];
}
if (res.rows.length > 0) {
this.noData = false;
this.pageIndex++;
this.list = [...this.list,...res.rows];
} else {
if (res.pageNumber > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.noData = true;
}
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
},
gotoDetail(HQJCID) {
this.$router.push({name: "PremaritalCheckDetail", params: {HQJCID: HQJCID}});
},
}
}



